import { err, ok, Result } from './Result';

export function Try<T>(f: () => T): Result<Error, T> {
  try {
    return ok(f());
  } catch (e) {
    if (e instanceof Error) {
      return err(e);
    }
    return err(new Error(e));
  }
}
